.d-flex {
  display: flex; }

.a-center {
  align-items: center; }

.j-beetween {
  justify-content: space-between; }

/* page */
/* Header page */
.header-page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 42px;
  padding-bottom: 34px; }
  @media (max-width: 767px) {
    .header-page {
      flex-direction: column; } }

@media (max-width: 767px) {
  .header-page__col:first-child {
    margin-bottom: 24px; } }

.site-logo {
  display: inline-block;
  width: 178px;
  height: 20px; }
  .site-logo img {
    max-width: 100%;
    max-height: 100%; }

.header-page__btns {
  display: flex;
  align-items: center; }
  @media (max-width: 767px) {
    .header-page__btns {
      flex-direction: column; } }

.header-page__btn {
  display: flex;
  align-items: center; }
  .header-page__btn svg {
    flex-shrink: 0;
    margin-right: 15px; }
  .header-page__btn strong {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px; }

.header-page__btn:last-child {
  background: linear-gradient(180deg, #EB2655 0%, #72021D 100%);
  border-radius: 10px;
  min-height: 50px;
  padding-right: 15px;
  padding-left: 15px;
  margin-left: 30px;
  transition: all 0.3s; }
  .header-page__btn:last-child :hover {
    cursor: pointer; }
  @media (max-width: 767px) {
    .header-page__btn:last-child {
      margin-left: 0;
      margin-top: 10px; } }

.main-page {
  min-height: 100vh; }

.footer-page__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%; }
  @media (max-width: 991px) {
    .footer-page__item {
      flex-wrap: wrap;
      justify-content: center; } }
  @media (max-width: 991px) {
    .footer-page__item .footer-page__col {
      margin-bottom: 20px; } }

.footer-page__item:first-child {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding-bottom: 24px;
  margin-bottom: 30px; }
  @media (max-width: 768px) {
    .footer-page__item:first-child {
      padding-bottom: 20px;
      margin-bottom: 20px; } }

.footer-page__item:nth-child(2) {
  margin-bottom: 20px; }

.footer-page__bottom {
  background-color: #02081C;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 32px; }

.contact {
  display: flex;
  align-items: center; }
  @media (max-width: 767px) {
    .contact {
      flex-direction: column; } }

.contact__item:not(:last-child) {
  margin-right: 30px; }
  @media (max-width: 767px) {
    .contact__item:not(:last-child) {
      margin-right: 0;
      margin-bottom: 10px; } }

.contact__link {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  transition: all 0.3s; }

.footer-page__text {
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: rgba(255, 255, 255, 0.3); }
  @media (max-width: 767px) {
    .footer-page__text {
      font-size: 12px;
      line-height: 22px; } }

.footer-page__private {
  display: flex;
  align-items: center;
  margin: 0 auto; }
  .footer-page__private img {
    width: 40px;
    height: 40px;
    flex-shrink: 0; }
  .footer-page__private span {
    font-size: 14px;
    line-height: 17px;
    font-weight: 400;
    display: inline-block;
    margin-left: 10px; }
    @media (max-width: 767px) {
      .footer-page__private span {
        margin-left: 0;
        margin-top: 10px; } }
  @media (max-width: 767px) {
    .footer-page__private {
      flex-direction: column;
      text-align: center; } }

.footer-page__copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%; }
  @media (max-width: 767px) {
    .footer-page__copyright {
      justify-content: center; } }
  .footer-page__copyright span {
    color: #3D486C;
    font-size: 16px;
    line-height: 20px;
    font-weight: 400; }
  @media (max-width: 767px) {
    .footer-page__copyright span:last-child {
      display: none; } }
  .footer-page__copyright div {
    display: inline-block;
    text-decoration: underline; }

/* default */
